/**
 * Scientific Calculator Logic
 * Provides safe evaluation of mathematical expressions with scientific functions
 */

export type CalculatorResult = {
  result: number | null;
  error: string | null;
  isError: boolean;
};

/**
 * Safely evaluates a mathematical expression with scientific functions
 * @param expression - The mathematical expression to evaluate
 * @returns CalculatorResult object with result or error
 */
export function evaluateExpression(expression: string): CalculatorResult {
  if (!expression || expression.trim() === '') {
    return {
      result: 0,
      error: null,
      isError: false
    };
  }

  try {
    // Clean the expression
    let cleanExpression = expression.trim();
    
    // Replace display symbols with JavaScript operators
    cleanExpression = cleanExpression.replace(/×/g, '*');
    cleanExpression = cleanExpression.replace(/÷/g, '/');
    
    // Replace scientific functions with Math equivalents
    cleanExpression = cleanExpression.replace(/sin\(/g, 'Math.sin(');
    cleanExpression = cleanExpression.replace(/cos\(/g, 'Math.cos(');
    cleanExpression = cleanExpression.replace(/tan\(/g, 'Math.tan(');
    cleanExpression = cleanExpression.replace(/log\(/g, 'Math.log10(');
    cleanExpression = cleanExpression.replace(/ln\(/g, 'Math.log(');
    cleanExpression = cleanExpression.replace(/sqrt\(/g, 'Math.sqrt(');
    cleanExpression = cleanExpression.replace(/π/g, 'Math.PI');
    cleanExpression = cleanExpression.replace(/e/g, 'Math.E');
    
    // Validate the expression contains only allowed characters
    const allowedPattern = /^[0-9+\-*/().πe\s,Math.sincotaglqrtPIE]+$/;
    if (!allowedPattern.test(cleanExpression)) {
      return {
        result: null,
        error: 'Invalid characters in expression',
        isError: true
      };
    }

    // Use Function constructor for safer evaluation than eval
    const result = new Function('Math', `"use strict"; return (${cleanExpression})`)(Math);
    
    // Check for invalid results
    if (typeof result !== 'number' || !isFinite(result)) {
      return {
        result: null,
        error: 'Invalid calculation result',
        isError: true
      };
    }

    // Round to avoid floating point precision issues
    const roundedResult = Math.round(result * 1e10) / 1e10;
    
    return {
      result: roundedResult,
      error: null,
      isError: false
    };
    
  } catch (error) {
    return {
      result: null,
      error: 'Invalid expression',
      isError: true
    };
  }
}

/**
 * Formats a number for display in the calculator
 * @param num - The number to format
 * @returns Formatted string representation
 */
export function formatNumber(num: number): string {
  if (Math.abs(num) >= 1e10 || (Math.abs(num) < 1e-6 && num !== 0)) {
    return num.toExponential(6);
  }
  
  // Remove trailing zeros after decimal point
  return parseFloat(num.toPrecision(12)).toString();
}

/**
 * Converts degrees to radians for trigonometric functions
 * @param degrees - Angle in degrees
 * @returns Angle in radians
 */
export function degreesToRadians(degrees: number): number {
  return degrees * (Math.PI / 180);
}

/**
 * Converts radians to degrees
 * @param radians - Angle in radians
 * @returns Angle in degrees
 */
export function radiansToDegrees(radians: number): number {
  return radians * (180 / Math.PI);
}
