"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { evaluateExpression, formatNumber, type CalculatorResult } from '@/lib/calculator';

export default function ScientificCalculator() {
  const [display, setDisplay] = useState('0');
  const [expression, setExpression] = useState('');
  const [isResult, setIsResult] = useState(false);
  const [angleMode, setAngleMode] = useState<'deg' | 'rad'>('deg');

  const handleNumber = (num: string) => {
    if (isResult) {
      setDisplay(num);
      setExpression(num);
      setIsResult(false);
    } else {
      const newDisplay = display === '0' ? num : display + num;
      setDisplay(newDisplay);
      setExpression(expression + num);
    }
  };

  const handleOperator = (op: string) => {
    if (isResult) {
      setExpression(display + op);
      setIsResult(false);
    } else {
      setExpression(expression + op);
    }
    setDisplay(display + op);
  };

  const handleFunction = (func: string) => {
    const functionStr = func + '(';
    if (isResult) {
      setExpression(functionStr);
      setDisplay(functionStr);
      setIsResult(false);
    } else {
      setExpression(expression + functionStr);
      setDisplay(display + functionStr);
    }
  };

  const handleConstant = (constant: string) => {
    if (isResult) {
      setDisplay(constant);
      setExpression(constant);
      setIsResult(false);
    } else {
      setDisplay(display + constant);
      setExpression(expression + constant);
    }
  };

  const handleEquals = () => {
    let evalExpression = expression;
    
    // Handle angle mode for trigonometric functions
    if (angleMode === 'deg') {
      evalExpression = evalExpression.replace(/sin\(([^)]+)\)/g, 'sin($1*Math.PI/180)');
      evalExpression = evalExpression.replace(/cos\(([^)]+)\)/g, 'cos($1*Math.PI/180)');
      evalExpression = evalExpression.replace(/tan\(([^)]+)\)/g, 'tan($1*Math.PI/180)');
    }

    const result: CalculatorResult = evaluateExpression(evalExpression);
    
    if (result.isError) {
      setDisplay(result.error || 'Error');
      setExpression('');
    } else {
      const formattedResult = formatNumber(result.result || 0);
      setDisplay(formattedResult);
      setExpression(formattedResult);
      setIsResult(true);
    }
  };

  const handleClear = () => {
    setDisplay('0');
    setExpression('');
    setIsResult(false);
  };

  const handleBackspace = () => {
    if (display.length > 1) {
      const newDisplay = display.slice(0, -1);
      const newExpression = expression.slice(0, -1);
      setDisplay(newDisplay);
      setExpression(newExpression);
    } else {
      setDisplay('0');
      setExpression('');
    }
  };

  const toggleAngleMode = () => {
    setAngleMode(angleMode === 'deg' ? 'rad' : 'deg');
  };

  return (
    <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 p-4">
      <Card className="w-full max-w-md shadow-2xl">
        <CardHeader className="text-center pb-4">
          <CardTitle className="text-2xl font-bold text-gray-800">
            Scientific Calculator
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Display */}
          <div className="bg-gray-900 text-white p-4 rounded-lg">
            <div className="text-right text-3xl font-mono min-h-[3rem] flex items-center justify-end overflow-hidden">
              {display}
            </div>
          </div>

          {/* Angle Mode Toggle */}
          <div className="flex justify-center">
            <Button
              variant="outline"
              size="sm"
              onClick={toggleAngleMode}
              className="text-xs"
            >
              {angleMode.toUpperCase()}
            </Button>
          </div>

          {/* Button Grid */}
          <div className="grid grid-cols-5 gap-2">
            {/* Row 1 - Functions */}
            <Button variant="secondary" onClick={() => handleFunction('sin')} className="text-xs">
              sin
            </Button>
            <Button variant="secondary" onClick={() => handleFunction('cos')} className="text-xs">
              cos
            </Button>
            <Button variant="secondary" onClick={() => handleFunction('tan')} className="text-xs">
              tan
            </Button>
            <Button variant="secondary" onClick={() => handleFunction('log')} className="text-xs">
              log
            </Button>
            <Button variant="secondary" onClick={() => handleFunction('ln')} className="text-xs">
              ln
            </Button>

            {/* Row 2 - More Functions */}
            <Button variant="secondary" onClick={() => handleFunction('sqrt')} className="text-xs">
              √
            </Button>
            <Button variant="secondary" onClick={() => handleConstant('π')} className="text-xs">
              π
            </Button>
            <Button variant="secondary" onClick={() => handleConstant('e')} className="text-xs">
              e
            </Button>
            <Button variant="outline" onClick={handleBackspace} className="text-xs">
              ⌫
            </Button>
            <Button variant="destructive" onClick={handleClear}>
              C
            </Button>

            {/* Row 3 - Parentheses and operators */}
            <Button variant="outline" onClick={() => handleOperator('(')}>
              (
            </Button>
            <Button variant="outline" onClick={() => handleOperator(')')}>
              )
            </Button>
            <Button variant="outline" onClick={() => handleOperator('^')}>
              ^
            </Button>
            <Button variant="outline" onClick={() => handleOperator('÷')}>
              ÷
            </Button>
            <Button variant="outline" onClick={() => handleOperator('×')}>
              ×
            </Button>

            {/* Row 4 - Numbers */}
            <Button onClick={() => handleNumber('7')}>7</Button>
            <Button onClick={() => handleNumber('8')}>8</Button>
            <Button onClick={() => handleNumber('9')}>9</Button>
            <Button variant="outline" onClick={() => handleOperator('-')}>
              -
            </Button>
            <Button variant="outline" onClick={() => handleOperator('+')}>
              +
            </Button>

            {/* Row 5 - Numbers */}
            <Button onClick={() => handleNumber('4')}>4</Button>
            <Button onClick={() => handleNumber('5')}>5</Button>
            <Button onClick={() => handleNumber('6')}>6</Button>
            <Button onClick={() => handleNumber('1')}>1</Button>
            <Button onClick={() => handleNumber('2')}>2</Button>

            {/* Row 6 - Numbers and equals */}
            <Button onClick={() => handleNumber('3')}>3</Button>
            <Button onClick={() => handleNumber('0')} className="col-span-2">
              0
            </Button>
            <Button onClick={() => handleNumber('.')}>.</Button>
            <Button 
              onClick={handleEquals} 
              className="bg-blue-600 hover:bg-blue-700 text-white"
            >
              =
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
